<template>
  <section id="formPanel">
    <el-form :model="formData" label-width="120px" size="small">
      <el-form-item label="">
        <el-checkbox v-model="formData['readonly']"></el-checkbox>
      </el-form-item>
      <el-form-item label="">
        <el-checkbox v-model="formData['disabled']"></el-checkbox>
      </el-form-item>
      <el-form-item label="">
        <el-checkbox v-model="formData['editable']"></el-checkbox>
      </el-form-item>
      <el-form-item label="">
        <el-checkbox v-model="formData['clearable']"></el-checkbox>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="formData['size']" placeholder="">
          <el-option key="medium" label="" value="medium"> </el-option>
          <el-option key="small" label="" value="small"> </el-option>
          <el-option key="mini" label="" value="mini"> </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="formData['type']" placeholder="" @change="changePlaceholder">
          <el-option key="year" label="" value="year"> </el-option>
          <el-option key="month" label="" value="month"> </el-option>
          <el-option key="date" label="" value="date"> </el-option>
          <el-option key="dates" label="" value="dates"> </el-option>
          <el-option key="week" label="" value="week"> </el-option>
          <el-option key="datetime" label="" value="datetime"> </el-option>
          <el-option key="datetimerange" label="" value="datetimerange"> </el-option>
          <el-option key="daterange" label="" value="daterange"> </el-option>
          <el-option key="monthrange" label="" value="monthrange"> </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="" v-show="!isRange">
        <el-input v-model="formData['placeholder']" placeholder=":"></el-input>
      </el-form-item>
      <el-form-item label="" v-show="isRange">
        <el-input v-model="formData['start-placeholder']" placeholder=":"></el-input>
      </el-form-item>
      <el-form-item label="" v-show="isRange">
        <el-input v-model="formData['end-placeholder']" placeholder=":"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formData['format']" placeholder=":yyyy-MM-dd"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formData['value-format']" placeholder=":yyyy-MM-dd"></el-input>
      </el-form-item>
    </el-form>
  </section>
</template>

<script>
// import { debounce } from "throttle-debounce";

export default {
  props: {
    value: Object
  },
  data() {
    return {
      formData: {
        editable: true,
        clearable: true
      },
      isRange: false
    };
  },
  watch: {
    value: {
      immediate: true,
      handler(value) {
        this.formData = value;
      }
    }
  },
  methods: {
    changePlaceholder() {
      if (this.formData.type == "datetimerange" || this.formData.type == "daterange" || this.formData.type == "monthrange") {
        this.isRange = true;
      } else {
        this.isRange = false;
      }

      let format = this.formData.format;
      switch (this.formData.type) {
        case "year":
          format = "yyyy";
          break;
        case "month":
          format = "MM";
          break;
        case "date":
          format = "yyyy-MM-dd";
          break;
        case "dates":
          format = "yyyy-MM-dd";
          break;
        case "daterange":
          format = "yyyy-MM-dd";
          break;
        case "datetime":
          format = "yyyy-MM-dd HH:mm:ss";
          break;
        case "datetimerange":
          format = "yyyy-MM-dd HH:mm:ss";
          break;
        case "monthrange":
          format = "yyyy-MM";
          break;
        default:
          break;
      }

      this.$set(this.formData, "format", format);
      this.$set(this.formData, "value-format", format);
    }
  }
};
</script>

<style></style>
